.sidebar
{
    flex: 1;
    border-right: .5px solid gray;
    min-height: 100vh;
    background-color: #fff;
}
.top
{
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-bottom: .5px solid gray;
}
.top .logo
{
    font-size: 20px;
    font-weight: bold;
    color: purple;
}
.center
{
    padding-left: 10px;
}
.center ul
{
    list-style: none;
}
.center ul .title
{
    font-size: 10px;
    font-weight: bold;
    color: #999;
    margin-top: 15px;
    margin-bottom: 5px;
}
.center ul li
{
    display: flex;
    align-items: center;
    padding: 5px;
    cursor: pointer;
}
.center ul li:hover
{
    background-color: #ece8ff;
}
.center ul li .icon
{
    font-size: 18px;
    color: #7451f8;
}
.center ul li span
{
    font-size: 13px;
    font-weight: 600;
    color: #888;
    margin-left: 10px;
}
.bottom
{
    display: flex;
    align-items: center;
    margin: 10px;
}
.bottom .color-option
{
    width: 20px;
    height: 20px;
    border-radius: 5px;
    border: 1px solid #7451f8;
    margin: 5px;
    cursor: pointer;
}
.bottom .color-option:nth-child(1)
{
    background-color: whitesmoke;
}
.bottom .color-option:nth-child(2)
{
    background-color: #333;
}